import { Form, message } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../../../helper/function";
import { ApiPublicService } from "../../../../../../services/taskService";
import { MyFormItem } from "../../../../../core/Input";
import { MyBox } from "../../../../../core/MyBox";
import { MyButton } from "../../../../../core/MyButton";
import AssignApiComponent from "./AssignApiComponent";

export default function DetailComponent(props: any) {
    const keyEdit = useParams().key;
    const projectKey: string | undefined = useParams().projectKey;
    const [isLoading, setIsLoading] = useState(false);
    const nav = useNavigate();

    const [name, setName] = useState('');
    const [_public, setPublic] = useState(false);

    const loadDataDetail = useCallback(() => {
        if (keyEdit !== undefined) {
            setIsLoading(true);
            ApiPublicService.GetDetail(projectKey, keyEdit)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setName(rs.data.name)
                        setPublic(rs.data.status);
                    }
                })
                .finally(() => setIsLoading(false))
        }
    }, [setIsLoading, projectKey, keyEdit])

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function handleSubmitForm() {
        var body = {
            name: name,
            status: _public ? 1 : 0
        };

        setIsLoading(true);
        if (keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        ApiPublicService.Add(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    nav(`/tools/tasks/projects/${projectKey}/codeManage/document/publish/edit/${rs.data.key}`);
                } else setIsLoading(false);
            })
    }

    function edit(body: any) {
        ApiPublicService.GetDetail(projectKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    ApiPublicService.Edit(projectKey, keyEdit, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                            }
                        })
                        .finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    function handleDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa data này không?"
        })
            .then((rs) => {
                if (rs.isConfirmed) {
                    doDelete();
                }
            })
    }

    function doDelete() {
        ApiPublicService.GetDetail(projectKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    ApiPublicService.Delete(projectKey, keyEdit, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                nav(`/tools/tasks/projects/${projectKey}/codeManage/document/publish`);
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function handleGetLink(type: number) {
        const link: string = `${window.location.origin}/docApis/${keyEdit}`;

        if (type === 0) {
            navigator.clipboard.writeText(link);
            message.success("Save link to clipboard");
        } else {
            nav(`/docApis/${keyEdit}`);
        }
    }

    return (
        <MyBox
            title="Chỉnh sửa"
            isLoading={isLoading}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Switch
                    label="Public"
                    value={_public}
                    setValue={setPublic}
                    readOnly={keyEdit === undefined} />

                <MyButton
                    htmlType="submit"
                    type="success"
                    isLoading={isLoading}
                    value={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    value="Xóa"
                    onClick={handleDelete} />

                <MyButton
                    value="Get link"
                    type="secondary"
                    onClick={() => { handleGetLink(0) }} />

                <MyButton
                    value="Go to public page"
                    type="warning"
                    onClick={() => { handleGetLink(1) }} />

                <AssignApiComponent
                    projectKey={projectKey}
                    keyEdit={keyEdit} />
            </Form>
        </MyBox>
    )
}