import { Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Fn } from "../../../../../../helper/function";
import { ApiPublicService } from "../../../../../../services/taskService";
import { MyFormItem } from "../../../../../core/Input";
import { Box } from "../../../../../core/MyBox";
import { MyButton } from "../../../../../core/MyButton";

export default function AssignApiComponent(props: any) {
    const { projectKey, keyEdit } = props;
    const [data, setData] = useState<any[]>([]);
    const [checkAll, setCheckAll] = useState();

    const loadData = useCallback(() => {
        ApiPublicService.GetListDetail(projectKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setData(rs.data)
                }
            })
    }, [projectKey, keyEdit])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleChangeSwitch(val: boolean | undefined, index: any) {
        data[index].check = val;
        setData([...data]);
    }

    function onChangeCheckAll(val: boolean) {
        data.map((_data: any, index: number) => {
            data[index].check = val;
            setData([...data]);
            return 1;
        })
    }

    function handleClickSave() {
        var body = {
            details: data
        };
        ApiPublicService.UpdateDetail(projectKey, keyEdit, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
    }

    function renderData() {
        return data.map((data: any, index: number) => {
            return (
                <Box key={index}>
                    <Row>
                        <Col span={12}>
                            <Link to={`/tools/tasks/projects/${projectKey}/codeManage/document/${data.folderKey}/apis/${data.apiKey}`}>{data.name}</Link>
                        </Col>
                        <Col span={12}>
                            <MyFormItem.Switch value={data.check} setValue={(val: boolean) => handleChangeSwitch(val, index)} />
                        </Col>
                    </Row>
                </Box>
            )
        })
    }
    return (
        <Box>
            <Row gutter={12}>
                <Col span={24}>
                    <MyFormItem.Checkbox
                        label="Check all"
                        value={checkAll}
                        setValue={setCheckAll}
                        onChange={onChangeCheckAll} />
                </Col>
                <Col span={24}>
                    {renderData()}
                </Col>
                <Col span={24}>
                    <MyButton
                        type="success"
                        value="Lưu danh sách public API"
                        onClick={handleClickSave} />
                </Col>
            </Row>
        </Box>
    )
}